import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FacilityBookingListComponent } from './facility-booking-list/facility-booking-list.component';
import { FacilityBookingStatusComponent } from './facility-booking-status.component';
import { FacilityBookingConfirmationComponent } from './facility-booking-confirmation/facility-booking-confirmation.component';
import { FacilityBookingStatusResolverService } from './facility-booking-status-resolver.service';


const routes: Routes = [
	{ path : '',component: FacilityBookingStatusComponent,
		children : [
			{ path: 'pending', component: FacilityBookingListComponent,
				children : [{
					path         : ':type/:id',
					component    : FacilityBookingConfirmationComponent,
					resolve: {
						data: FacilityBookingStatusResolverService
					}
				}]
			},
			{ path: 'confirmed', component: FacilityBookingListComponent,
				children : [{
					path         : ':type/:id',
					component    : FacilityBookingConfirmationComponent,
					resolve: {
						data: FacilityBookingStatusResolverService
					}
				}]
			},
			{ path: 'cancelled', component: FacilityBookingListComponent,
				children : [{
					path         : ':type/:id',
					component    : FacilityBookingConfirmationComponent,
					resolve: {
						data: FacilityBookingStatusResolverService
					}
				}]
			},
			{ path: 'rejected', component: FacilityBookingListComponent,
				children : [{
					path         : ':type/:id',
					component    : FacilityBookingConfirmationComponent,
					resolve: {
						data: FacilityBookingStatusResolverService
					}
				}]
			},
			{ path: 'history', component: FacilityBookingListComponent}
		]
	}
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FacilityBookingStatusRoutingModule { }
