import { Component, OnInit, ViewChild, HostListener, ChangeDetectorRef, ViewEncapsulation } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { SessionService } from 'src/app/core/session/session.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { Router, ActivatedRoute } from '@angular/router';
import { SharedService } from 'src/app/shared/services/shared.service';
import * as moment from 'moment';
import { MatDrawer } from '@angular/material/sidenav';
import { MatDialog } from '@angular/material/dialog';
import { ModalComponentComponent } from 'src/app/shared/components/modal-component/modal-component.component';

@Component({
  selector: 'app-facility-booking-list',
  templateUrl: './facility-booking-list.component.html',
  styleUrls: ['./facility-booking-list.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class FacilityBookingListComponent implements OnInit {
  @ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;
  drawerMode: 'over';

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  gridSourceData:any;
  columnData:any;
  bookingListData:any;
  bookingSearch:string;
  facilityListData = [];
  statusTypeData = [];
  filterFacility:any = {
    apartmentFacilityID : '',
    facilityBookingStatusID : '',
    eventName:null,
    bookedOn_From : null,
    bookedOn_To: null,
    eventDate_From: null,
    eventDate_To: null,
  };
  modalService:any;
  urlType:string;
  isBookingDataLoaded:boolean = false;
  isAdminLogin:boolean = false;
  totalItems:number = 0;

  constructor(
    private facilityService:FacilityService,
    private sessionService:SessionService,
    private lookupService: LookupService,
    private router: Router,
    private activeRouter: ActivatedRoute,
    private sharedService:SharedService,
    private changeDetectorRef: ChangeDetectorRef,
    public dialog: MatDialog,
  ) {}

  onBackdropClicked() {
    // Get the current activated route
    let route = this.activeRouter;
    while ( route.firstChild ) {
      route = route.firstChild;
    }
    // Go to the parent route
    this.router.navigate([`./${this.urlType}`], {relativeTo: this.activeRouter.parent});
    this.matDrawer.close();
    // Mark for check
    this.changeDetectorRef.markForCheck();
  }

  @HostListener('window:onStatusFacilityBooking', ['$event.detail'])
  changeStatus(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    let route = this.activeRouter;
    while ( route.firstChild ) {
      route = route.firstChild;
    }
    this.sharedService.setMatDrawerId(dataRecord.apartmentFacilityBookingId);
    this.router.navigate([this.router.url+'/', 'edit', dataRecord.apartmentFacilityBookingId], {relativeTo: route});
    this.changeDetectorRef.markForCheck();
  }

  @HostListener('window:onEditFacilityBooking', ['$event.detail'])
  editBooking(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    this.router.navigate(['ams/facility/edit-booking',dataRecord.apartmentFacilityBookingId]);
  }

  @HostListener('window:onViewFacilityBooking', ['$event.detail'])
  viewBooking(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    this.router.navigate(['ams/facility/view-booking',dataRecord.apartmentFacilityBookingId]);
  }

  @HostListener('window:ondeleteMeeting', ['$event.detail'])
  ondeleteSlot(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    this.modalService.showConfirmModal(dataRecord.apartmentFacilityBookingId);
  }

  isAdmin(){
    this.isAdminLogin = (this.sessionService.roleTypeName == 'Admin' || this.sessionService.roleTypeName == 'Staff') ? true : false;
  }

  resetField() {
    for(let facKey in this.filterFacility) {
      this.filterFacility[facKey] = null;
    }
    if(this.urlType == 'history') {
      let yesterday = moment(new Date()).subtract(1, 'days').utc().format();
      this.filterFacility.eventDate_From = moment(yesterday).subtract(2, 'month').utc().format();
    }
    this.getBookingList();
  }

  searchData() {
    if(this.bookingSearch != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.bookingSearch;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  getPrintParams(event) {
    this.datagrid.exportdata(event, this.urlType);
  }

  getBookingList() {
    this.isBookingDataLoaded = false;
    let bookingListParams:any = {
      apartmentId: this.sessionService.apartmentId,
      eventName: this.filterFacility.eventName,
      apartmentFacilityID: this.filterFacility.apartmentFacilityID,
      eventDate_From: this.filterFacility.eventDate_From,
      eventDate_To: this.filterFacility.eventDate_To,
      bookedOn_From: this.filterFacility.bookedOn_From,
      bookedOn_To: this.filterFacility.bookedOn_To,
    }
    if(this.urlType == 'history') {
      this.filterFacility.eventDate_To = moment(new Date()).subtract(1, 'days').utc().format();
      bookingListParams.eventDate_To = this.filterFacility.eventDate_To;
      bookingListParams.facilityBookingStatusID = this.filterFacility.facilityBookingStatusID;
    } else if(this.urlType == 'confirmed') {
      bookingListParams.facilityBookingStatusID = 188;
    } else if(this.urlType == 'pending') {
      bookingListParams.facilityBookingStatusID = 189;
    } else if(this.urlType == 'cancelled') {
      bookingListParams.facilityBookingStatusID = 208;
    }else {
      bookingListParams.facilityBookingStatusID = 385;
    }
    this.facilityService.getApartmentFacilityBookingsByApartmentId(bookingListParams).subscribe((res:any) => {
      if(res.length > 0) {
        this.totalItems = res.length;
        this.gridSourceData = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.bookingListData = new jqx.dataAdapter(this.gridSourceData);
      }
	    this.isBookingDataLoaded = true;
    })
  }

  ngOnInit() {
    this.activeRouter.url.subscribe((data)=>{
      this.urlType = data[0].path;
    })
    //facility List
    let categoryParams = {
      apartmentId: this.sessionService.apartmentId
    }
    this.facilityService.getApartmentFacilitiesByApartmentId(categoryParams).subscribe((res:any) => {
      this.facilityListData = res;
    })
    //facility status
    if(this.urlType == 'history') {
      let yesterday = moment(new Date()).subtract(1, 'days').utc().format();
      this.filterFacility.eventDate_From = moment(yesterday).subtract(2, 'month').utc().format();
      let statusParams = {
        LookupTypeId: 40
      }
      this.lookupService.getLookupValueByLookupTypeId(statusParams).subscribe((res:any) => {
        this.statusTypeData = res;
      });
    }
    this.getBookingList();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
      text: 'Booking Id',
      datafield: 'serialNo',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Facility Name',
      datafield: 'apartmentFacilityName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Event Date',
      datafield: 'bookedForDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer,
    },{
      text: 'Event Time',
      datafield: 'bookedFromTime',
        cellsrenderer: (row, column, value) => {
          let time,fromTime,toTime = this.bookingListData.loadedData[row].bookedToTime;
          if(value && toTime)  {
          fromTime = moment(value,'HH:mm:ss').format('hh:mm A');
          toTime = moment(toTime,'HH:mm:ss').format('hh:mm A');
          time = `${fromTime} - ${toTime}`
          } else {
            time = '-'
          }
        return '<div class="jqx-custom-inner-cell">'+ time +'</div>'
      },
      cellsalign: 'center', 
      align: 'center',
      minwidth: 160,
      renderer: columnrenderer
    },{
      text: 'Total Hours',
      datafield: 'bookedToTime',
      cellsrenderer: (row, column, value) => {
        let time,toTime,fromTime = this.bookingListData.loadedData[row].bookedFromTime;
        if(fromTime && value) {
          fromTime = moment(fromTime,'HH:mm:ss').format();
          toTime = moment(value,'HH:mm:ss').format();
          time = moment.duration(moment(toTime).diff(moment(fromTime)));
          if(time.minutes() == 0) {
            time = `${time.hours()}hrs`
          } else {
            time = `${time.hours()}hrs ${time.minutes()}mins`
          }
          if(time == '23hrs 58mins') {
            time = '24hrs'
          }
        } else {
          time = '-'
        }
        return '<div class="jqx-custom-inner-cell ml-4">'+ time +'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Rate Type',
      datafield: 'rateBaseIdName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Cost',
      datafield: 'amount',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Discount',
      datafield: 'discount',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Final Bill',
      datafield: 'totalBillAmount',
      cellsalign: 'center', 
      align: 'center',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer,
    },{
      text: 'Booked For',
      datafield: 'isBookingforGuest',
      cellsrenderer: (row, column, value) => {
        value =  value ? 'Guest' : 'Unit User';
        return '<div class="jqx-custom-inner-cell">'+ value +'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Status',
      datafield: 'facilityBookingStatusId_Label',
      cellsrenderer: (row, column, label) => {
        let className  = label == 'Rejected' ? 'cancelled' : label.toLowerCase();
        if(this.urlType == 'history') {
          return '<div class="jqx-custom-inner-cell"><span class="w-100 badge small min text-capitalize '+ className +'">'+ label +'</span></div>'
        }
        else {
          return '<div class="jqx-custom-inner-cell"><span onClick="statusFacilityBooking('+ row +')" class="w-100 badge small min text-capitalize link '+ className +'">'+ label +'</span></div>'
        }
      },
      cellsalign: 'center', 
      align: 'center',
      minwidth: 100,
      renderer: columnrenderer
    },{
      text: 'Actions',
      cellsalign: 'center',
      cellsrenderer: (row: number): string => {
        if(this.urlType == 'history') {
          return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-3" onClick="viewFacilityBooking('+ row +')"><i class="fa fa-eye icon view" aria-hidden="true"></i></a></div>'
        } else {
          return '<div class="simple-actions">'
          +'<a href="javascript:void(0)" class="mr-3" onClick="editFacilityBooking('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a>'
          +'<a href="javascript:void(0)" class="mr-2" onClick="showConfirmDelete(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a>'
          +'</div>'
        }
      },
      minwidth: 80,
      renderer: columnrenderer
    }];
    //delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( item => {
      if(item != null) {
        var params = {
          apartmentFacilityBookingId: item,
          deleteBy: parseInt(this.sessionService.userId)
        }
        this.facilityService.deleteApartmentFacilityBooking(params).subscribe((res:any) => {
          this.sharedService.setUnitListDeleteIndex(null);
          this.getBookingList();
        });
      }
    });
  }
}

function statusFacilityBooking(row){
  var event = new CustomEvent('onStatusFacilityBooking', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).statusFacilityBooking = statusFacilityBooking;


function editFacilityBooking(row){
  var event = new CustomEvent('onEditFacilityBooking', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editFacilityBooking = editFacilityBooking;

function viewFacilityBooking(row){
  var event = new CustomEvent('onViewFacilityBooking', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewFacilityBooking = viewFacilityBooking;

function showConfirmDelete(row){
  var event = new CustomEvent('ondeleteMeeting', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDelete = showConfirmDelete;
